$bg-color: #EDF2EC;
$nav-color: #010201;
$orange-color: #E06733;
$green-color: #499A18;
$light-grey-color: #717171;
$dark-grey-color: #636060;
$border-color: #E3E1D5;


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0; }


a {
    color: black;
    text-decoration: none; }
li {
    list-style-type: none; }
nav {}

.green {
    color: $green-color; }

.container {
    position: relative;
    max-width: 1222px;
    margin: 0 auto; }

.button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $orange-color;
    border-radius: 5px;
    background-color: transparent;
    width: 161px;
    height: 50px;
    &:hover {
        background-color: $orange-color;
        color: white; } }

.header {
    background-color: $bg-color;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 134px;
    &__navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        width: 1220px; }
    &__logo {
        display: flex;
        flex-direction: row;
        align-items: flex-end; }
    &__nav-list {
        display: flex;
        flex-direction: row;
        align-items: center; }
    &__logo-text {
        font-size: 16px;
        line-height: 11px; }
    &__list-item {
        font-size: 16px;
        margin-right: 46px;
        &:last-child {
            margin-right: 0; }
        a {
            color: $nav-color;
            &:hover {
                color: $orange-color; } } } }


.welcome {
    background: $bg-color;
    min-height: 532px;
    padding-top: 62px;
    &__content-container {
        min-width: 1222px;
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__text-container {
        width: 549px;
        height: 340px; }
    &__header {
        font-size: 45px;
        line-height: 50px;
        color: $light-grey-color;
        margin-bottom: 42px; }
    &__text {
        max-width: 525.44px;
        color: $dark-grey-color;
        font-size: 20px;
        line-height: 35px;
        margin-bottom: 48px; }
    &__button {
        border: 2px solid $orange-color;
        border-radius: 10px;
        font-size: 16px;
        width: 158px;
        height: 44px; } }

.about {
    min-height: 666px;
    padding-top: 93px;
    &__content-container {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__text-container {
        width: 605px;
        height: 350px; }
    &__header {
        max-width: 463px;
        font-size: 40px;
        color: $orange-color;
        margin-bottom: 40px; }
    &__text {
        line-height: 30px;
        font-size: 20px;
        color: $dark-grey-color; } }

.service {
    background-color: $bg-color;
    min-height: 1300px;
    padding-top: 40px;
    &__header-bar {
        max-width: 1007px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 90px; }
    &__header {
        font-size: 40px;
        width: 306px;
        color: $green-color; }
    &__buttons-bar {
        display: flex; }
    &__button {
        color: $orange-color;
        font-size: 20px;
        margin-right: 38px;
        &:last-child {
            margin-right: 0; } }
    &__services {
        height: 955px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-content: space-between; }
    &__service-card {
        width: 330px; }
    &__text-container {
        padding-top: 10px;
        width: 330px;
        height: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 1px solid $border-color;
        border-radius: 0px 0px 20px 20px; }
    &__card-img {
        display: block;
        width: 330px;
        border-radius: 20px 20px 0px 0px; }
    &__card-header {
        color: $orange-color;
        font-size: 20px;
        margin-bottom: 10px; }
    &__card-text {
        color: $light-grey-color;
        font-size: 16px; } }

.prices {
    padding-top: 40px;
    min-height: 560px;
    &__content-container {
        height: 405px;
        display: flex;
        flex-flow: column wrap;
        align-content: space-between;
        img {
            display: flex;
            align-self: flex-end; } }
    &__prices {}
    &__price-box {
        border: 1px solid $border-color;
        border-radius: 20px;
        width: 332px;
        height: 282px;
        display: flex;
        justify-content: center;
        align-items: center; }
    &__item-box {
        height: 214px;
        display: flex;
        flex-direction: column;
        justify-content: space-between; }
    &__item {
        padding: 16px 40px;
        background-color: $bg-color;
        border-radius: 20px;
        width: 250px;
        height: 50px; }
    &__item-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 170px;
        height: 18px; }
    &__header1 {
        font-size: 40px;
        line-height: 48px;
        margin-bottom: 60px; }
    &__header2 {
        text-align: right;
        font-size: 40px;
        line-height: 50px;
        margin-bottom: 38px; }
    &__text-container {
        display: flex;
        flex-flow: column wrap;
        align-items: flex-end;
        width: 459px;
        margin-bottom: 39px; }
    &__button {
        color: $orange-color;
        font-size: 20px; } }

.contacts {
    min-height: 550px;
    background-color: $bg-color;
    padding-top: 53px;
    &__content-container {
        img {
            position: absolute; } }
    &__city-selector {
        position: absolute;
        top: 95px;
        right: 0; }
    &__dropdown {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 35px;
            transform: translateY(-50%);
            width: 16px;
            height: 16px;
            display: block;
            background: url('../icons/accordion_btn-dark.svg') no-repeat right;
            pointer-events: none; } }
    &__select {
        //reset
        outline: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        border: none;
        //styles
        width: 450px;
        height: 50px;
        padding: 5px 35px 5px 20px;
        background-color: #D6E7D2;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        font-size: 20px;
        line-height: 40px;
        color: $light-grey-color; }
    &__header {
        color: $green-color;
        font-size: 40px;
        position: absolute;
        top: 7px;
        right: 50%;
        transform: translate(50%, 0); } }

.footer {
    min-height: 130px;
    &__content-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 130px; }
    &__data {
        font-size: 20px;
        line-height: 50px;
        word-spacing: 20px;
        display: flex; }
    &__name {
        font-size: 20px;
        line-height: 50px; }
    &__social-list {
        height: 50px;
        display: flex;
        align-items: center;
        a {
            padding-right: 20px;
            &:last-child {
                padding-right: 0; } } } }
